"use strict";
const Logger = require('js-Logger');
const Fabric = require('fabric');
const { icons } = require('../utils/icons');
Logger.useDefaults();
export default class EnxWb {
  constructor(_config) {
    this.canvas = null;
    this.config = _config;
    this.canvasId = _config.canvasId;
    this.options = {};
    this.eraserColor = "#FFFFFF";
    this.scheme = _config.scheme || 'default';
    this.room = null;
    this.streamStarted = false;
    this.collaborationStarted = false;
    switch (this.scheme) {
      case 'default':
        this.bgColor = '#FFFFFF';
        this.toolbarBGColor = "#EEEEEE";
        this.brushColor = '#000000';
        this.icon = {
          size: 'small',
          type: 'square',
          BGColor: '#f3f3f3',
          FGColor: '#000000',
          border: '1px solid grey'
        }
        break;
      case 'custom': {
        this.bgColor = _config.custom.bgColor || '#FFFFFF';
        this.toolbarBGColor = _config.custom.toolbarBGColor || "#EEEEEE";
        this.brushColor = _config.custom.brushColor || '#000000'
        this.icon = _config.custom.icon || {
          size: 'small',
          type: 'square',
          BGColor: '#f3f3f3',
          FGColor: '#000000',
          border: '1px solid grey'
        }
      }
        break;
      default:
        this.bgColor = '#FFFFFF';
        this.toolbarBGColor = "#EEEEEE";
        this.iconColor = '#000000';
        this.brushColor = '#000000',
          this.icon = {
            size: 'small',
            type: 'square',
            BGColor: '#f3f3f3',
            FGColor: '#000000',
            border: '1px solid grey'
          }
        break;
    }
    this.defaults = {
      text: {
        text: 'Sample Text', fill: '#ff0000', fontFamily: 'Times New Roman', textAlign: 'left', fontStyle: 'normal', fontSize: 20, padding: 5
      },
      rect: {
        width: 200, height: 100, left: 50, top: 50, fill: '', stroke: '#000000', strokeWidth: 2
      },
      circle: {
        radius: 50, fill: '', stroke: '#000000', strokeWidth: 2
      },
      triangle: {
        width: 100, height: 100, fill: '', stroke: '#000000', strokeWidth: 2
      },
      ellipse: {
        fill: '', stroke: '#000000', strokeWidth: 2, rx: 100, ry: 50
      },
      square: {
        width: 100, height: 100, left: 150, top: 50, fill: '', stroke: '#000000', strokeWidth: 2
      },
      line: {
        fill: '#000000', stroke: '#000000', strokeWidth: 2
      }
    }
  }

  createToolbar() {
    this.toolbarTemplate = `<ul class="wb-toolbar-ul">
    <li><button class="enxButton" id="disableDM" title="${this.icons.select.name}" >${this.icons.select.src}</button></li>
    <li id="enxPencil"><button class="enxButton" id="enableDM" title="${this.icons.pencil.name}" >${this.icons.pencil.src}</button>
      <ul class="enxDropdown pencil-dropdown">
        <li><input type="color" class="enxInput" name="favcolor" id="changeDC" title="Brush color" value="${this.brushColor}">  </li>
        <li><input type="number" class="enxInput" name="width"  width="100" id="changeDW" value="2" title="Change Brush Width"/></li>
      </ul>
    </li>    
    <li id="enxFill"><button class="enxButton" id='fillBackground' title="${this.icons.fill.name}">${this.icons.fill.src}</button>
    <ul class="enxDropdown background-dropdown">      
      <li><button class="enxButton fileContainer" title="${this.icons.bgImage.name}">
            ${this.icons.bgImage.src}
          <input type="file" name="image"  id="changeBGImage"/>   
        </button>
      </li>
    </ul>
    </li>
    <li><button class="enxButton" id="addText" title="${this.icons.text.name}">${this.icons.text.src}</button></li>  
    <li><button class="enxButton" id="addCircle" title="${this.icons.circle.name}">${this.icons.circle.src}</button></li>
    <li><button class="enxButton" id="addSqaure" title="${this.icons.square.name}">${this.icons.square.src}</button></li>
    <li><button class="enxButton" id="addEllipse" title="${this.icons.ellipse.name}">${this.icons.ellipse.src}</button></li>
    <li><button class="enxButton" id="addTriangle" title="${this.icons.triangle.name}">${this.icons.triangle.src}</button></li>
    <li><button class="enxButton" id="addRect" title="${this.icons.rect.name}">${this.icons.rect.src}</button></li>
    <li><button class="enxButton" id="addLine" title="${this.icons.line.name}">${this.icons.line.src}</button></li>    
    <li><button class="enxButton fileContainer" title="${this.icons.image.name}">
      ${this.icons.image.src}
      <input type="file" name="addImage" id="addImage"/>   
    </button></li>
    <li id="enxEraser"><button class="enxButton" id="eraser" title="${this.icons.eraser.name}">${this.icons.eraser.src}</button>
      <ul class="enxDropdown eraser-dropdown">    
        <li><input type="number" class="enxInput" name="width"  width="100" id="changeEraserWidth" value="2" title="Change Eraser Width"/></li>
      </ul>
    </li>
    <li><button class="enxButton" id="undo" title="${this.icons.undo.name}">${this.icons.undo.src}</button></li>
    <li><button class="enxButton" id="redo" title="${this.icons.redo.name}">${this.icons.redo.src}</button></li>
    <li><button class="enxButton" id="download_exnWB" title="${this.icons.download.name}">${this.icons.download.src}</button></li>
    <li><button class="enxButton" id="deleteAO" title="${this.icons.delete.name}"  >${this.icons.delete.src}</button></li>
    <li><button class="enxButton" id="clearAll" title="${this.icons.clearAll.name}"  >${this.icons.clearAll.src}</button></li> 
    <div style="display:none;" class="vertical-line"></div>
    <li style="display:none;" ><button class="enxButton red-button" id="startStreaming" title="${this.icons.streaming.name}" >${this.icons.streaming.src}</button></li>
    <li style="display:none;"><button class="enxButton red-button"  id="startCollaboration" title="${this.icons.collaboration.name}" >${this.icons.collaboration.src}</button></li>       
    </ul>`;
  }

  create(room = null) {
    if (!this.canvas) {
      this.room = room;
      this.icons = icons;
      this.createToolbar();
      this.initializeCanvas();
    }
  }

  initializeCanvas() {
    let _this = this;
    this.totArray = [];
    this.i = 0;
    this.reflag = false;
    this.canvas = new fabric.Canvas(this.canvasId, {
      isDrawingMode: true,
      backgroundColor: this.bgColor
    });
    var svgHeight = '20px';
    var svgWidth = '20px';
    var buttonHeight = '30px';
    var buttonWidth = '35px';
    var inputHeight = '25px';
    var inputWidth = '30px'
    switch (this.icon.size) {
      case 'small':
        svgHeight = '20px';
        svgWidth = '20px';
        buttonHeight = '30px';
        buttonWidth = '35px';
        inputHeight = '25px';
        inputWidth = '30px'
        break;
      case 'medium':
        svgHeight = '25px';
        svgWidth = '25px';
        buttonHeight = '40px';
        buttonWidth = '40px';
        inputHeight = '36px';
        inputWidth = '40px'
        break;
      case 'large':
        svgHeight = '40px';
        svgWidth = '30px';
        buttonHeight = '50px';
        buttonWidth = '50px';
        inputHeight = '46px';
        inputWidth = '50px';
        break;
    }
    var borderRadius = "none";
    switch (this.icon.type) {
      case 'square':
        borderRadius = 'none';
        break;
      case 'rounded-square':
        borderRadius = '5px';
        break;
    }
    var canvasWidth = this.config.initialWidth || innerWidth;
    var canvasHeight = this.config.initialHeight || innerHeight
    var parent_div = document.querySelector(`#${this.canvasId}`).parentElement.parentElement;
    var toolbar_element = document.createElement("div");
    toolbar_element.style.backgroundColor = this.toolbarBGColor;
    toolbar_element.style.border = '1px solid';
    toolbar_element.style.width = `${canvasWidth}`;
    toolbar_element.style.padding = '10px 0';
    toolbar_element.innerHTML = _this.toolbarTemplate;
    parent_div.insertBefore(toolbar_element, parent_div.firstElementChild);
    var svgIcons = document.querySelectorAll('.enxSVG');
    var i;
    for (i = 0; i < svgIcons.length; i++) {
      svgIcons[i].style.height = svgHeight;
      svgIcons[i].style.width = svgWidth;
      svgIcons[i].style.fill = this.icon.FGColor;
    }
    var buttons = document.querySelectorAll('.enxButton');
    var i;
    for (i = 0; i < buttons.length; i++) {
      buttons[i].style.height = buttonHeight;
      buttons[i].style.width = buttonWidth;
      buttons[i].style.background = this.icon.BGColor;
      buttons[i].style.border = this.icon.border;
      buttons[i].style.borderRadius = borderRadius;
    }
    var input = document.querySelectorAll('.enxInput');
    var i;
    for (i = 0; i < input.length; i++) {
      input[i].style.height = inputHeight;
      input[i].style.width = inputWidth;
    }
    this.canvas.selection = true;
    fabric.Object.prototype.transparentCorners = true;
    this.canvas.setHeight(canvasHeight);
    this.canvas.setWidth(canvasWidth);
    this.canvas.freeDrawingBrush.color = this.brushColor;
    this.canvas.freeDrawingBrush.width = 2;
    this.canvas.on('object:added', function (opt) {
      _this.totArray[_this.i] = opt.target;
      _this.i++;
    });
    this.options = {
      drawMode: {
        enable: () => {
          _this.canvas.isDrawingMode = true;
        },
        disable: () => {
          _this.canvas.isDrawingMode = false;
        }
      },
      drawingBrush: {
        setColor(color) {
          _this.canvas.freeDrawingBrush.color = `${color}`;
        },
        setWidth(width) {
          _this.canvas.freeDrawingBrush.width = `${width}`;
        }
      },
      background: {
        setColor(color) {
          _this.canvas.backgroundImage = "";
          _this.canvas.setBackgroundColor('');
          _this.canvas.setBackgroundColor(color);
          _this.canvas.renderAll();
        },
        setImage(id) {
          var file = document.querySelector(`#${id}`).value;
          var exts = ['jpg', 'jpeg', 'png'];
          if (file.length <= 0) {
            alert("Please select a file from local drive.");

            return false;
          }
          var ext = file.split('.');
          ext = ext.reverse();
          var reader = new FileReader();
          reader.onload = function (event) {
            var imgObj = new Image;
            imgObj.src = event.target.result;
            imgObj.onload = function () {
              var image = new fabric.Image(imgObj);
              _this.canvas.setBackgroundColor('#ffffff');
              _this.canvas.setBackgroundImage(image, _this.canvas.renderAll.bind(_this.canvas), {
                scaleY: _this.canvas.height / _this.canvas.height,
                scaleX: _this.canvas.width / _this.canvas.width
              });
            }
          }
          reader.readAsDataURL(document.querySelector(`#${id}`).files[0]);
        }
      }
    }
    //Event handling for toolbar
    var addText = document.querySelector("#addText");
    addText.addEventListener("click", function () {
      _this.addText();
    });
    var addSquare = document.querySelector("#addSqaure");
    addSquare.addEventListener("click", function () {
      _this.addSquare();
    });
    var addCircle = document.querySelector("#addCircle");
    addCircle.addEventListener("click", function () {
      _this.addCircle();
    });
    var addRect = document.querySelector("#addRect");
    addRect.addEventListener("click", function () {
      _this.addRect();
    });
    var addLine = document.querySelector("#addLine");
    addLine.addEventListener("click", function () {
      _this.addLine();
    });
    var addEllipse = document.querySelector('#addEllipse');
    addEllipse.addEventListener('click', function () {
      _this.addEllipse();
    });
    var addTriangle = document.querySelector('#addTriangle');
    addTriangle.addEventListener('click', function () {
      _this.addTriangle();
    });
    var downloadEnxWB = document.querySelector("#download_exnWB");
    downloadEnxWB.addEventListener('click', function () {
      _this.downloadENXWB();
    });
    var undo = document.querySelector("#undo");
    undo.addEventListener('click', function () {
      _this.undo();
    });
    var redo = document.querySelector("#redo");
    redo.addEventListener('click', function () {
      _this.redo();
    });    
    var eraser = document.querySelector("#eraser");
    eraser.addEventListener("click", function () {
      if (typeof _this.canvas.backgroundColor == 'object') {
        _this.eraserColor = '#ffffff';
      } else {
        _this.eraserColor = _this.canvas.backgroundColor;
      }
      _this.canvas.freeDrawingBrush.color = _this.eraserColor;
    });
    var changeBGImage = document.querySelector("#changeBGImage");
    changeBGImage.addEventListener("change", function () {
      _this.options.background.setImage('changeBGImage');
    });
    var changeDrawColor = document.querySelector("#changeDC");
    changeDrawColor.addEventListener("change", function () {
      _this.options.drawingBrush.setColor(changeDrawColor.value);
    });
    var changeEraserWidth = document.querySelector("#changeEraserWidth");
    changeEraserWidth.addEventListener("change", function () {
      _this.options.drawingBrush.setWidth(changeEraserWidth.value);
    });

    var changeDW = document.querySelector("#changeDW");
    changeDW.addEventListener("change", function () {
      _this.options.drawingBrush.setWidth(changeDW.value);
    });
    var deleteAO = document.querySelector("#deleteAO");
    deleteAO.addEventListener("click", function (params) {
      _this.removeActiveObject()
    });
    var clearAll = document.querySelector("#clearAll");
    clearAll.addEventListener("click", function (params) {
      _this.clearAll()
    });
    var enableDM = document.querySelector("#enableDM");
    enableDM.addEventListener("click", function () {
      _this.options.drawingBrush.setWidth(changeDW.value);
      _this.options.drawMode.enable();
      _this.options.drawingBrush.setColor(changeDrawColor.value);
    });
    var disableDM = document.querySelector("#disableDM");
    disableDM.addEventListener("click", function () {
      _this.options.drawMode.disable();
    });
    var addImg = document.querySelector('#addImage');
    addImg.addEventListener("change", function () {
      _this.addImage('addImage');
    });
    var startStreaming = document.querySelector("#startStreaming");
    startStreaming.addEventListener("click", function () {
      if (_this.streamStarted == false) {
        _this.startStreaming();
      }
      else {
        _this.stopStreaming();
      }
    });

    var startCollaborationBtn = document.querySelector("#startCollaboration");
    startCollaborationBtn.addEventListener("click", function () {
      if (_this.collaborationStarted == false) {
        _this.startCollaboration();
        startCollaborationBtn.classList.add('blink-image');
        
      }
      else {
        _this.stopCollaboration();
        startCollaborationBtn.classList.remove('blink-image')
      }
    });

    var parentDiv = document.querySelector(`#${_this.canvasId}`).parentNode;
    parentDiv.addEventListener('click', function (event) {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('touchstart', function () {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('touchend', function () {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('touchmove', function () {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('keyup', function () {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('keypress', function () {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('mousedown', function () {
      _this.collaborate();
    }, false);
    parentDiv.addEventListener('keydown', function () {
      _this.collaborate();
    }, false);
    if(_this.room !== null)
    {
      _this.room.addEventListener("user-data-received", function (event) {
        var startCollaborationBtn = document.querySelector("#startCollaboration");
        if (event.message.message.collabEnxWB) {
          _this.collaborationStarted = true;
          startCollaborationBtn.style.display = 'none';
          document.querySelector(`#${_this.canvasId}`).parentNode.parentNode.style.display = "block";
        }
        else if (event.message.message.collabEnxWB == false) {
          _this.collaborationStarted = false;
          startCollaborationBtn.style.display = 'block';
          document.querySelector(`#${_this.canvasId}`).parentNode.parentNode.style.display = "none";
        }
        else if (event.message.message.drawEnxWB) {
          var data = event.message.message.draw;
          _this.canvas.loadFromJSON(data, _this.canvas.renderAll.bind(_this.canvas), function (_o, _object) { });
        }
  
      });
    }
    
  }
  addText(config) {
    var config = config || this.defaults.text;
    var text = new fabric.IText(config.text, {
      fill: config.fill,
      hasControls: true,
      hasBorders: true,
      hasRotatingPoint: true
    });
    text.set({
      fontFamily: config.fontFamily,
      textAlign: config.textAlign,
      fontStyle: config.fontStyle,
      fontSize: config.fontSize,
      padding: config.padding
    });
    this.options.drawMode.disable();
    this.canvas.add(text);
    this.canvas.setActiveObject(text);
    this.canvas.centerObject(text);
    text.setCoords();
    this.canvas.renderAll();
  }
  addImage(fileID) {
    let _this = this;
    var file = document.querySelector(`#${fileID}`).value;
    var exts = ['jpg', 'jpeg', 'png'];
    if (file.length <= 0) {
      alert("Please select a file from local drive.");
      return false;
    }
    var ext = file.split('.');
    ext = ext.reverse();
    var reader = new FileReader();
    reader.onload = function (event) {
      var imgObj = new Image;
      imgObj.src = event.target.result;
      imgObj.onload = function () {
        var image = new fabric.Image(imgObj);
        _this.options.drawMode.disable();
        _this.canvas.add(image);
        _this.canvas.centerObject(image);
        _this.canvas.setActiveObject(image);
        image.setCoords();
        _this.canvas.renderAll();
      }
    }
    reader.readAsDataURL(document.querySelector(`#${fileID}`).files[0]);
  }
  addCircle(config) {
    var config = config || this.defaults.circle;
    var circle = new fabric.Circle(config);
    this.options.drawMode.disable();
    this.canvas.add(circle);
    this.canvas.centerObject(circle);
    circle.setCoords();
    this.canvas.setActiveObject(circle);
    this.canvas.renderAll();
  }
  addRect(config) {
    var config = config || this.defaults.rect;
    var rect = new fabric.Rect(config);
    this.options.drawMode.disable();
    this.canvas.add(rect);
    this.canvas.centerObject(rect);
    rect.setCoords();
    this.canvas.setActiveObject(rect);
    this.canvas.renderAll();
  }
  addSquare(config) {
    var config = config || this.defaults.square;
    var square = new fabric.Rect(config);
    this.options.drawMode.disable();
    this.canvas.add(square);
    this.canvas.centerObject(square);
    square.setCoords();
    this.canvas.setActiveObject(square);
    this.canvas.renderAll();
  }
  addTriangle(config) {
    var config = config || this.defaults.triangle;
    var triangle = new fabric.Triangle(config);
    this.options.drawMode.disable();
    this.canvas.add(triangle);
    this.canvas.centerObject(triangle);
    triangle.setCoords();
    this.canvas.setActiveObject(triangle);
    this.canvas.renderAll();
  }
  addLine(config) {
    var config = config || this.defaults.line;
    var line = new fabric.Line([0, 0, 200, 0], config);
    this.options.drawMode.disable();
    this.canvas.add(line);
    this.canvas.centerObject(line);
    line.setCoords();
    this.canvas.setActiveObject(line);
    this.canvas.renderAll();
  }
  addEllipse(config) {
    var config = config || this.defaults.ellipse;
    var ellipse = new fabric.Ellipse(config);
    this.options.drawMode.disable();
    this.canvas.add(ellipse);
    this.canvas.centerObject(ellipse);
    ellipse.setCoords();
    this.canvas.setActiveObject(ellipse);
    this.canvas.renderAll();
  }
  undo() {
    if (this.i > 0) {
      this.reflag = true;
      this.canvas.remove(this.totArray[this.i - 1]);
      this.i--;
    }
  }
  redo() {
    if (this.i < this.totArray.length) {
      this.canvas.add(this.totArray[this.i]);
      if (!this.reflag) {
        this.i++;
      }
    }
  }
  clearAll() {
    this.canvas.clear();
    this.options.background.setColor(this.bgColor);
    this.canvas.renderAll();
  }
  startStreaming() {
    var _this = this;
    this.room.startCanvas({
      "canvasSelector": this.canvasId,
      "fps": 23,'refreshFn' : function(){_this.canvas.add()},
    }, function (event) {
      if (event.result == 0) {
        var startStreaming = document.querySelector("#startStreaming");
        _this.streamStarted = true;
        startStreaming.classList.add('blink-image')
      }
    })
  }
  stopStreaming() {
    var _this = this;
    this.room.stopCanvas(function (event) {
      var startStreaming = document.querySelector("#startStreaming");
      _this.streamStarted = false;
      startStreaming.classList.remove('blink-image')
    });
  }
  startCollaboration() {
    var _this = this;
    _this.collaborationStarted = true;
    _this.room.sendUserData({ "collabEnxWB": true }, true, [], function () { });
  }
  stopCollaboration() {
    var _this = this;
    _this.collaborationStarted = false;
    _this.room.sendUserData({ "collabEnxWB": false }, true, [], function () { });
  }
  downloadENXWB(){
    var _this = this;
    var data = _this.canvas.toDataURL();
    var dlink = document.createElement('a');
    dlink.download = "EnxWB";
    dlink.href = data;
    document.body.appendChild(dlink);
    dlink.click();
    dlink.remove();
  }
  collaborate() {
    var _this = this;
    var JSONData = _this.canvas.toJSON();
    var dataToSend = {
      "drawEnxWB": true,
      "draw": JSONData
    };
    if (_this.collaborationStarted) {
      _this.room.sendUserData(dataToSend, true, [], function () { });
    }
  }

  removeActiveObject() {
    var activeObject = this.canvas.getActiveObject();
    this.canvas.remove(activeObject);
    this.canvas.renderAll();
  }
  getWbData() {
    return this.canvas.toJSON();
  }
  setWbData(data) {
    this.canvas.loadFromJSON(data, this.canvas.renderAll.bind(this.canvas), function (_o, _object) { });
  }
  shareWith(clientId) {
    if (this.collaborationStarted) {
      this.room.sendUserData({ "collabEnxWB": true }, false, [clientId], function () { });
      var JSONData = this.canvas.toJSON();
      var dataToSend = {
        "drawEnxWB": true,
        "draw": JSONData
      };
      this.room.sendUserData(dataToSend, true, [clientId], function () { });
    }
  }
  destroy() { }
}